import React, { useEffect, useState } from 'react'
import './style.css'
import * as backimgtop from '../../assets/Clean Walk/Levels_Screen/BG_top.svg';

import * as scanback from '../../assets/Clean Walk/Levels_Screen/LS_qr_base.png'
import * as lsqr from '../../assets/images/Levels_Screen/LS_qr.png'
import * as lv9img from '../../assets/images/Levels_Screen/LS_dot1.png'
import * as lv10img from '../../assets/images/Levels_Screen/LS_dot2.png'

import { useHistory } from 'react-router-dom'
import { loader } from 'components/Common/core';
import HeaderComponent from 'components/Common/HeaderComponent';

import { levelstore } from '../../store/levelStore';
import { instancestore } from '../../store/InstanceStore';

import { useParams } from 'react-router-dom';
import { useAuth } from '../../store/authStore';


const TaskListScreen = () => {
  const { level, loadlevelwithid } = levelstore();
  const { instance, loadinstance } = instancestore();
  const { auth } = useAuth()
  const history = useHistory();
  const [spinner, setspinner] = useState(true)

  const [levelicon, setlevelicon] = useState('')

  const navigate = (id: number) => {
    history.replace('/' + params.instanceId + '/TaskScreen/' + id)
  }
  let params = useParams();

  useEffect(() => {

    if (!auth) {
      history.replace("/")
    }
    if (!instance) {
      loadinstance()
    }
    loadlevelwithid(params.instanceId)
    if (localStorage.getItem('isFirsttime') === null) {
      loader()
    } setspinner(false)


  }, [])

  const loadinstanceicon = (instanceid: any) => {
    let a = " ";
    (instance) && instance.map((obj: any) => {
      // console.log(">> title >> : ", title, obj.l_title);

      if (String(instanceid) === String(obj.id)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);
        a = obj.mascot
      } else { null }
    })
    return a
  }

  const loadicon = (levelid: any) => {
    // console.log(levelid, "============", level);
    let a = "";
    (level) && level.map((obj: any) => {
      // console.log(">> title >> : ", obj);

      if (String(levelid) === String(obj.l_level)) {
        // console.log(">> obj.l_icon >> : ", obj.l_icon);
        a = obj.l_icon
      } else { null }
    })
    return a
  }
  const RenderList = (obj: any) => {
    console.log(obj);

    return (<div className={`taskicon${obj.l_level}`} onClick={() => {
      navigate(obj.l_level)
    }}>
      <img className='tasklevelimagemedium' src={obj.l_icon} />
    </div>
    )
  }

  return (
    <div className="back_color_main">

      <>
        <div>
          <img className='bgimgtop' src={backimgtop} />
        </div>
        <HeaderComponent />

        <div className='task_scanqrcontainer_main'>
          <div className='task_scanqrcontainer_sub'>
            <img className='task_scanback' src={scanback} />
            <div
              onClick={() => {
                history.replace('/ClueListScreen')
              }}
              className='task_scanqrcontainer'>
              <img className='task_scanqr' src={lsqr} />
            </div>
          </div>
        </div>

        <div className='taskslist_container'>
          <div className='taskslist'>

            <img className='tasklslogo' src={loadinstanceicon(params.instanceId)} />
            {/* {list[7].active ? */}

            {
              (level) && level.map(RenderList)
            }

            {/* <div className='taskicon8' onClick={() => {
              navigate(8)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(8)} />
            </div> */}

            {/* <div className='taskicon7' onClick={() => {
              navigate(7)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(7)} />
            </div> */}

            {/* <div className='taskicon6' onClick={() => {
              navigate(6)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(6)} />
            </div> */}

            <div className='taskicon9'>
              <img className='tasklevelimagesmalldisabled' src={lv9img} />
            </div>

            {/* <div className='taskicon5' onClick={() => {
              navigate(5)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(5)} />
            </div> */}

            <div className='taskicon10'>
              <img className='tasklevelimageExsmalldisabled' src={lv10img} />
            </div>

            {/* <div className='taskicon4' onClick={() => {
              navigate(4)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(4)} />
            </div>
            <div className='taskicon3'
              onClick={() => {
                navigate(3)
              }}>
              <img className='tasklevelimagemedium' src={loadicon(3)} />
            </div>
            <div className='taskicon2' onClick={() => {
              navigate(2)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(2)} />
            </div>
            <div className='taskicon1' onClick={() => {
              navigate(1)
            }}>
              <img className='tasklevelimagemedium' src={loadicon(1)} />
            </div> */}

          </div>
        </div>

        <div className="bgbottombluebar" />

      </>


    </div>
  )
}


export default TaskListScreen
